<h1 id="安装" tabindex="-1"><a class="header-anchor" href="#安装" aria-hidden="true">#</a> 安装</h1>
<h2 id="环境要求" tabindex="-1"><a class="header-anchor" href="#环境要求" aria-hidden="true">#</a> 环境要求</h2>
<blockquote>
<ul>
<li>PHP &gt;= 7.0</li>
<li><a href="http://php.net/manual/en/book.curl.php" target="_blank" rel="noopener noreferrer">PHP cURL 扩展<OutboundLink/></a></li>
<li><a href="http://php.net/manual/en/book.openssl.php" target="_blank" rel="noopener noreferrer">PHP OpenSSL 扩展<OutboundLink/></a></li>
<li><a href="http://php.net/manual/en/book.simplexml.php" target="_blank" rel="noopener noreferrer">PHP SimpleXML 扩展<OutboundLink/></a></li>
<li><a href="http://php.net/manual/en/book.fileinfo.php" target="_blank" rel="noopener noreferrer">PHP fileinfo 拓展<OutboundLink/></a></li>
</ul>
</blockquote>
<p>Laravel 5 拓展包: <a href="https://github.com/overtrue/laravel-wechat" target="_blank" rel="noopener noreferrer">overtrue/laravel-wechat<OutboundLink/></a></p>
<h2 id="安装-1" tabindex="-1"><a class="header-anchor" href="#安装-1" aria-hidden="true">#</a> 安装</h2>
<p>使用 <a href="http://getcomposer.org/" target="_blank" rel="noopener noreferrer">composer<OutboundLink/></a>:</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>$ <span class="token function">composer</span> require overtrue/wechat:~4.0 -vvv
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>